
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import SelectDown from 'components/SelectDown/selectdown.vue'
export default {
props: {

selectList1:{
type:Array,
default:[]
},

selectList2:{
type:Array,
default:[]
},
TabDefault1:{
type:Object,
default: function () {
return {}
}
},
TabDefault2:{
type:Object,
default: function () {
return {}
}
},
},
data() {
return {
showSelect1:false,
showSelect2:false,
showPopupPicker1:false,
showPopupPicker2:false,
sVal1:this.TabDefault1,
sVal2:this.TabDefault2,
popupVal1:[],
popupVal2:[]
}
},
methods: {
toSelect1(){
this.showSelect1 = !this.showSelect1;
this.showSelect2 = false;
},
toSelect2(){
this.showSelect2 = !this.showSelect2;
this.showSelect1 = false;
},
selectChange1(val) {

this.$emit('on-change1',val);
},
selectChange2(val) {

this.$emit('on-change2',val);
},
},
components:{
PopupPicker,
SelectDown
}
}



