
import LoadMore from 'vux/src/components/load-more/index.vue'

import slide from "@/components/Slide/slide";
export default {
components: {
slide,
LoadMore
},
props: {
liIndex: {

type: Number,
default: 0
},
conIndex: {

type: Number,
default: -1
},
listClick: {

type: Function,
default: function() {}
},
conClick: {

type: Function,
default: function() {}
},
loadingLi: Boolean, //左边列表加载loading
loadingCon: Boolean, //右边列表加载loading
data: {
type: Array,
default: function() {
return [{
label: ''
}]
}
},
dataList: {
type: Array,
default: function() {
return [{
label: ''
}]
}
}
},
data() {
return {
panes: this.data,
panesList: this.dataList,
onLiIndex: this.liIndex,
onConIndex: this.conIndex,
};
},
mounted() {
this.$nextTick(() => {
this.setScrollPosition();
});
},
methods: {
setScrollPosition() {
if (this.panes.length !== 0) {
const li = this.$refs.myBoxLi.querySelectorAll('li')[this.onLiIndex];
let top1 = this.setScrollTop(li, this.$refs.list, this.$refs.myBoxLi);
this.$refs.scroll1.scrollTo(0, -top1, 0);
if (this.panesList[this.onConIndex]) {
const con = this.$refs.myBoxCon.querySelectorAll('li')[this.onConIndex];
let top2 = this.setScrollTop(con, this.$refs.con, this.$refs.myBoxCon);
this.$refs.scroll2.scrollTo(0, -top2, 0);
}
}
},
getOffsetRect(element) {
let elementRect = {
width: element.offsetWidth,
height: element.offsetHeight,
left: element.offsetLeft,
top: element.offsetTop
};
elementRect.right = elementRect.left + elementRect.width;
elementRect.bottom = elementRect.top + elementRect.height;
return elementRect;
},
setScrollTop(element, list, box) {
if (!element || !list || !box) {
return 0;
}
let rect = this.getOffsetRect(element);
let listRect = this.getOffsetRect(list);
let boxRect = this.getOffsetRect(box);
let top = rect.top;
if (rect.top + listRect.height > boxRect.height) {
top = boxRect.height - listRect.height;
}
if (boxRect.height <= listRect.height) {
top = 0;
}
return top;
},
TabLiClick(obj, index) {
if (this.onLiIndex === index || this.loadingCon) {
return
}
this.onLiIndex = index;
this.onConIndex = -1;
this.$refs.scroll2.scrollTo(0, 0, 0);
this.clearedOutList();
this.listClick(this.onLiIndex, this.onConIndex, obj);
},
TabConClick(obj, index) {
this.onConIndex = index;
this.conClick(this.onLiIndex, this.onConIndex, obj);
},
clearedOutList() {
this.panesList = [];
}
},
watch: {
data() {
this.panes = this.data;
},
dataList() {
this.panesList = this.dataList;
},
liIndex() {
this.onLiIndex = this.liIndex;
this.setScrollPosition();
this.clearedOutList();
this.listClick(this.onLiIndex, this.onConIndex, this.panes[this.onLiIndex]);
},
conIndex() {
this.onConIndex = this.conIndex;
this.setScrollPosition();
}
}
};



