export default {
    props:{
        selectShow:{
            type:Boolean,
            default:false
        },
        top:{
            type:Number,
            default:0
        },
        data:{
            type:Array,
            default:[]
        }
    },
    data(){
        return {
            list:this.data
           
        }
        
    },
    methods:{
        clickItem(item){
            this.$emit('input',item);//v-model赋值
            this.$emit('on-change',item);//改变
            this.$emit('update:selectShow',false);//关闭
        }
    }
}



