import BScroll from 'better-scroll'
export default {
	name: 'slide',
	props: {
		probeType: { 
			/**
			 * 有时候我们需要知道滚动的位置,默认值：0,可选值：1、2、3
			 * 1.会非实时屏幕滑动超过一定时间后）派发scroll 事件
			 * 2.会在屏幕滑动的过程中实时的派发 scroll 事件
			 * 3.不仅在屏幕滑动的过程中，而且在 momentum 滚动动画运行过程中实时派发 scroll 事件
			 */
			type: Number,
			default: 2
		},
		scrollX: {  //当设置为 true 的时候，可以开启横向滚动,当设置 eventPassthrough 为 'horizontal' 的时候，该配置无效。
			type: Boolean,
			default: true
		},
		scrollY: {  
			type: Boolean,
			default: false
		},
		stopPropagation: {  
			type: Boolean,
			default: true
		},

		snap: {
			type: [Boolean,Object],
			default: function(){
				return {
					threshold: .3,
					speed: 400,
				}
			}
		},
		currentPageIndex: {
			type: Number,
			default: 0
		},
	},
	mounted(){
		// 保证在DOM渲染完毕后初始化better-scroll
		this.$nextTick(() => { 
			this._initScroll();		 
		});
	},
	methods: {
		_initScroll() {			
			if (!this.$refs.slide) {
				return;
			}
			var u = navigator.userAgent;
			var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
			var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
			let clickType;
			if(isAndroid){
				clickType = true;
			}else{
				clickType = false;
			}
			// better-scroll的初始化
			this.slide = new BScroll(this.$refs.slide, {
				probeType: this.probeType,
				scrollX: this.scrollX,
				scrollY: this.scrollY,
				click:clickType,
				// stopPropagation: this.stopPropagation,
				snap: this.snap,
				momentum:false,  // 当快速在屏幕上滑动一段距离的时候, 设置false,关闭动画
				
			})
			// 判断tab_content 来判断是否调用 侧滑函数
			if(this.$refs.slideContent.getElementsByClassName('tab_content').length > 1 && this.scrollX){
				this.$refs.slideContent.style.width = this.$refs.slideContent.getElementsByClassName('tab_content').length * 100 + 'vw';
				this.slide.refresh();
				this.slide.on('scrollEnd',(pos) => {
					this.slide.refresh();
					if(Math.abs(pos.x) >= 0 && Math.abs(pos.y) <= 0){
						this._onScrollEnd();
					}
					
				})
			}
			
		},
		_onScrollEnd() {
			let pageIndex = this.slide.getCurrentPage().pageX;
			this.$emit('update:currentPageIndex', pageIndex);
		},

		disable() {	//禁用 better-scroll，DOM 事件（如 touchstart、touchmove、touchend）的回调函数不再响应。
			// 代理better-scroll的disable方法
			this.slide && this.slide.disable()
		},
		enable() {	//启用 better-scroll, 默认 开启。
			// 代理better-scroll的enable方法
			this.slide && this.slide.enable()
		},
		refresh() {
			// 代理better-scroll的refresh方法
			this.slide && this.slide.refresh()
		},
		goToPage(x, y, time, easing) {
			// 代理better-scroll的next方法
			this.slide && this.slide.goToPage(x, y, time, easing)
		},


	},
}



