export default {
    props:{
        titleBg:{
            type:String,
            // default:'#d5d5d6'
        },
        close: {
            type: Boolean,
            default: false
        },
        type: {},
        loaddata:{
            type:Function,
            default:function(){}
        }
    },
    mounted() {
        // let dom = this.$refs.colContentBox;
        // dom.style.height = dom.offsetHeight+'px';
    },
    data(){
        return {
            conShow:this.close
        }
    },
    watch:{
        close(val){//监听调用页面是否改变展示状态
          this.conShow =val
        }
    },
    methods:{
        slideToggle(){
            let dom = this.$refs.colContentBox;
            if(!this.conShow){
                dom.style.height = dom.offsetHeight+'px';
            }else{
                setTimeout(()=>{dom.style.height = 'auto';},300)                   
            }   
            this.conShow = !this.conShow;
        }
    }
}



