export default {
	mounted() {
	    var THIS = this;
	    var scrollToptimer = 0;
	    window.onscroll = function(){
			if(!THIS.$refs.backTop)return;
	    	var top = document.body.scrollTop || document.documentElement.scrollTop;
	    	if(!THIS.$refs.backTop){
	    		return;
	    	}
	    	if(top > 20){
	    		THIS.$refs.backTop.style.opacity = 1;
	    		THIS.$refs.backTop.style.transform = 'scale(1)';
	    		THIS.$refs.backTop.onclick = function(){
	    			scrollToptimer = setInterval(function () {
	    				top = document.body.scrollTop || document.documentElement.scrollTop;
	    				var speed = top / 4;
	    				if (document.body.scrollTop!=0) {
	    					document.body.scrollTop -= speed;
	    				}else {
	    					document.documentElement.scrollTop -= speed;
	    				}
	    				if(top <= 0){
	    					clearInterval(scrollToptimer);
	    				}
	    			},30)
	    		}
	    		
	    	}else{
	    		THIS.$refs.backTop.style.opacity = 0;
	    		THIS.$refs.backTop.style.transform = 'scale(0)';
	    	}
	    }
	},
}



