/**
 * 说明：
 *  该组件依赖 better-scroll 插件
 * 参数：
 *  type     类型1/2,1为普通列表,2为带添加按钮的列表
 *  value    通过v-model绑定控制弹出
 *  lists    选项列表，name为选项文字
 * 事件：
 *  action-sheet-add     点击添加
 *  action-sheet-item    点击选项
 *  action-sheet-cancel  点击取消
 */
import BScroll from 'better-scroll'
export default {
    props:{
        type:{
            type:Number,
            default:1
        },
        addInfo:{
            type:String,
            default:'添加就诊人'
        },

        value:{
            type:Boolean,
            default:false
        },
        lists:{
            type:Array,
            default(){
                return [
                        {
                            name:'周浩（示例）',
                            key:''
                        },
                        {
                            name:'王小利（示例）',
                            key:''
                        },
                    ]
            }
        }
       
    },
    data(){
        return {
            sheetShow:false,
        }
    },
    mounted() {
       this.sheetScroll =  new BScroll(this.$refs.sheetListWrapper,{click:true});
    },
    methods:{
        cancel(){
            this.$emit('action-sheet-cancel');
            this.$emit('input',false);
        },
        addItem(){
            this.$emit('action-sheet-add');
            this.$emit('input',false);
        },
        clickItem(item){
            
            this.$emit('action-sheet-item',item);
            this.$emit('input',false);
        }
    },
    watch:{
        value(v){
            // if(v){
            //     //记录当前的滚动位置
            //     this.scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
            //     // 使body脱离文档流
            //     //
            //     //document.body.classList.add('sheet-open');
            //     // 把脱离文档流的body拉上去！否则页面会回到顶部！ 
            //     //document.body.style.top = -this.scrollTop + 'px';
            // }else{
            //     //document.body.classList.remove('sheet-open');
            //     //document.body.scrollTop = document.documentElement.scrollTop = this.scrollTop;  
            // }
            setTimeout(()=>{
                this.sheetScroll.refresh()
            },20)
        }
    }
}



